import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Switch, Card, Result, Divider } from 'antd';
import { CheckOutlined } from '@ant-design/icons';

const MultiStepForm = () => {
    const [currentStep, setCurrentStep] = useState(0);
    const [formValues, setFormValues] = useState({
        participation: false,
        bibleStudies: 0,
        hours: 0,
    });
    const [isRegularPublisher, setRegularPublisher] = useState(false);

    useEffect(() => {
        if (window.Telegram && window.Telegram.WebApp) {
            window.Telegram.WebApp.ready();
            let paramValue = window.Telegram.WebApp.initDataUnsafe.start_param;
            setRegularPublisher(paramValue === "regular");
        }
    }, []);

    const next = () => {
        setCurrentStep(currentStep + 1);
    };

    const prev = () => {
        setCurrentStep(currentStep - 1);
    };

    const onChangeParticipation = (checked) => {
        setFormValues({ ...formValues, participation: checked });
    };

    const onChangeBibleStudies = (e) => {
        setFormValues({ ...formValues, bibleStudies: parseInt(e.target.value, 10) });
    };

    const onChangeHours = (e) => {
        setFormValues({ ...formValues, hours: parseInt(e.target.value, 10) });
    };

    const onSubmit = () => {
        const reportData = {
            chat_id:  window.Telegram.WebApp.initDataUnsafe.user.id,
            bible_studies_count: formValues.bibleStudies,
            hour_count: formValues.hours,
            in_the_ministry:  isRegularPublisher || formValues.participation,
            username: window.Telegram.WebApp.initDataUnsafe.user.username,
            fio: window.Telegram.WebApp.initDataUnsafe.user.user.first_name + " " + window.Telegram.WebApp.initDataUnsafe.user.user.last_name
        };

        fetch('https://jw-report-tg-bot-b19bbd582f8c.herokuapp.com/receive', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(reportData),
        })
            .then(response => response.json())
            .then(data => {
                window.Telegram.WebApp.showAlert('Success: ' + JSON.stringify(data));
                setCurrentStep(2); // Proceed to the final step
            })
            .catch((error) => {
                console.error('Error:', error);
                window.Telegram.WebApp.showAlert('Error: ' + error);
            });

        setCurrentStep(2)

        setTimeout(function(){
            window.Telegram.WebApp.close()
        }, 3000)
    };

    return (
        <Card>
            <Form
                onFinish={onSubmit}
                initialValues={formValues}
                style={{ padding: '30px 15px 20px' }}
                labelCol={{ span: 12 }}
                wrapperCol={{ span: 10 }}
                layout="horizontal"
            >
                {currentStep === 0 && (
                    <>
                        {isRegularPublisher ? (
                            <Form.Item label="Количество часов:">
                                <Input
                                    type="number"
                                    value={formValues.hours}
                                    onChange={onChangeHours}
                                    min="0"
                                />
                            </Form.Item>
                        ) : (
                            <Form.Item label="Участвовал в служении:">
                            <Switch checked={formValues.participation} onChange={onChangeParticipation} />
                             </Form.Item>
                            )
                        }
                        <Form.Item label="Количество изучений библии:">
                            <Input
                                type="number"
                                value={formValues.bibleStudies}
                                onChange={onChangeBibleStudies}
                                min="0"
                            />
                        </Form.Item>
                        <Button type="primary" ghost onClick={next} style={{width: "100%", marginTop: 35}}>
                            Далее
                        </Button>
                    </>
                )}
                {currentStep === 1 && (
                    <>
                        <p><i>Пожалуйста проверь точность данных, если что-то не так можешь вернуться на предыдущий шаг отредактировать отчет нажав "назад"</i></p>
                        <Divider orientation="left">Отчет за месяц:</Divider>
                        <div style={{fontWeight: "bold"}}>
                            {isRegularPublisher ? (
                            <p>Количество часов: {formValues.hours}</p>
                        ) : (
                            <p>Участвовал в служении: {formValues.participation ? 'Да' : 'Нет'}</p>
                        )}
                        <p>Количество изучений библии: {formValues.bibleStudies}</p>
                        </div>
                        <div style={{marginTop: 25, width: "100%", display: "flex"}}>
                            <Button style={{ margin: '0 8px', flexGrow: 1 }} onClick={prev}>
                                Назад
                            </Button>
                            <Button type="primary" htmlType="submit" style={{flexGrow: 1, background: 'green'}}>
                                Отправить <CheckOutlined />
                            </Button>
                        </div>
                    </>
                )}
                {currentStep === 2 && (
                    <Result
                        status="success"
                        title="Отчет успешно отправлен!"
                        subTitle="Спасибо тебе дорогой возвещатель за старания!"
                    />
                )}
            </Form>
        </Card>
    );
};

export default MultiStepForm;
